@import "https://fonts.googleapis.com/css2?family=Baloo+2:wght@400;700&family=Poppins:wght@300;400;600&display=swap";
.hero {
  background-color: #0000004d;
  background-image: url("fundo.fe61bc8a.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  padding-top: 220px;
  padding-bottom: 100px;
  position: relative;
}

.hero .container {
  position: relative;
}

.hero:before {
  content: "";
  opacity: .7;
  background-color: #000;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.hero__text {
  text-align: center;
  color: #fff;
  margin-top: 120px;
  margin-bottom: 64px;
  font-size: 40px;
  font-weight: 800;
  line-height: 56px;
}

@media (width >= 641px) and (width <= 1023px) {
  .hero {
    padding-top: 120px;
    padding-bottom: 80px;
  }

  .hero__logo {
    max-width: 60%;
    height: 64px;
  }

  .hero__text {
    margin: 40px 0;
    font-size: 28px;
    line-height: 36px;
  }
}

@media (width <= 640px) {
  .hero {
    background-position: center;
    padding: 40px 0;
  }

  .hero__logo {
    height: 40px;
  }

  .hero__text {
    margin: 24px 0;
    font-size: 22px;
    line-height: 24px;
  }
}

.event {
  padding: 80px 0;
}

.event .container {
  gap: 40px;
  display: flex;
}

.event--festa {
  background-image: linear-gradient(#439cfa, #007afd);
}

.event--confirmacao {
  background-image: linear-gradient(#007afd, #28a745);
}

.event--image-left .container {
  flex-direction: row-reverse;
}

.event__image {
  width: 100%;
  max-width: 360px;
}

.event__details__title {
  margin-bottom: 16px;
  font-size: 18px;
  font-weight: bold;
}

.event__description {
  line-height: 22px;
}

@media (width <= 640px) {
  .event .container {
    display: block;
  }

  .event__image {
    width: 100%;
  }

  .event__details {
    margin-bottom: 16px;
    margin-right: 0;
  }
}

.infos-bar {
  background-color: #fff;
  justify-content: space-between;
  padding: 16px 24px;
  display: flex;
}

.infos-bar__infos {
  gap: 40px;
  display: flex;
}

.infos-bar__infos__item {
  color: #000;
  font-size: 14px;
}

.infos-bar__infos__item b {
  color: #000;
  font-size: 18px;
  display: block;
}

@media (width <= 640px) {
  .infos-bar, .infos-bar__infos {
    display: block;
  }

  .infos-bar__infos__item {
    margin-bottom: 24px;
  }
}

@media (width <= 1024px) {
  .infos-bar__infos {
    gap: 16px;
  }
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --font-body: "Poppins", system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
  --font-heading: "Baloo 2", var(--font-body);
}

body {
  font-family: var(--font-body);
  color: #222;
  font-size: 16px;
  line-height: 1.5;
}

h1, h2, h3 {
  font-family: var(--font-heading);
  margin: 0 0 .5rem;
  line-height: 1.1;
}

.container {
  width: 100%;
  max-width: 960px;
  margin: 0 auto;
}

@media (width <= 1024px) {
  .container {
    max-width: 90%;
  }
}

@media (width <= 640px) {
  .container {
    max-width: 80%;
  }
}
